$breakpoint-sm: 768px;
$breakpoint-lg: 992px;
$breakpoint-xl: 1200px;

@mixin media($media) {
  @if $media == tablet {
    @media (min-width: $breakpoint-sm) {
      @content;
    }
  } @else if $media == desktop {
    @media (min-width: $breakpoint-lg) {
      @content;
    }
  } @else if $media == large {
    @media (min-width: $breakpoint-xl) {
      @content;
    }
  }
}
